import header from '@/components/header'
import { MessageBox, Indicator, Toast } from 'mint-ui'
import API from '@/api/vacation'

export default {
  components: { 'v-header': header },
  data () {
    return {
      headtitle: '请假审核',
      id: this.$store.getters.getVacationId,
      leaderOne: false,
      leaderTwo: false,
      leaderThree: false,
      lineOne: true,
      lineTwo: true,
      lineThree: true,
      vacationDetail: {
        zhgl_qj01001: '', // 姓名
        zhgl_qj01003: '', // 职务
        create_time: '',
        zhgl_qj01002: '', // 部门
        zhgl_qj01005: '', // 请假原因
        zhgl_qj01004: '', // 请假类型
        zhgl_qj01006: '', // 请假开始时间
        zhgl_qj01007: '', // 请假结束时间
        zhgl_qj01018: '', // 审核状态
        zhgl_qj01030: '', // 工时
        zhgl_qj01009: '', // 电话
        zhgl_qj01016: '' // 岗位代理人
      },
      statusOneObj: {
        '01': '待审核',
        '02': '同意',
        '03': '同意',
        '04': '同意',
        '05': '不同意'
      },
      statusTwoObj: {
        '02': '待审核',
        '03': '同意',
        '04': '同意',
        '05': '不同意'
      },
      statusThreeObj: {
        '03': '待审核',
        '04': '同意',
        '05': '不同意'
      },
      from: ''// 上个页面的名称
    }
  },
  beforeRouteEnter (to, from, next) {
    let fromname = from.name
    next(vm => {
      vm.from = fromname
      if (vm.from === 'home') {
        vm.queryName = ''
      } else {
        vm.queryName = 'checkList'
      }
    })
  },
  created () {
    this.getDetailByID()
  },

  methods: {
    getDetailByID: function () {
      let token = this.$store.getters.getUser.token
      let id = this.id
      Indicator.open({
        text: '正在加载...',
        spinnerType: 'fading-circle'
      })
      API.WINDOW_GET_CHECK_DETAIL(this, { token, id }).then((rep) => {
        let result = rep.body
        Indicator.close()
        if (result.success) {
          this.vacationDetail = result.data
          if (this.vacationDetail.zhgl_qj01011) {
            this.leaderOne = true
            this.lineOne = false
            this.lineTwo = false
            this.lineThree = false
            this.vacationDetail.leaderOneStatus = this.statusOneObj[result.data.zhgl_qj01018]
          }
          if (this.vacationDetail.zhgl_qj01014) {
            this.leaderTwo = true
            this.lineOne = true
            this.lineTwo = false
            this.lineThree = false
            this.vacationDetail.leaderOneStatus = '同意'
            this.vacationDetail.leaderTwoStatus = this.statusTwoObj[result.data.zhgl_qj01018]
          }
          if (this.vacationDetail.zhgl_qj01023) {
            this.leaderThree = true
            this.lineOne = true
            this.lineTwo = true
            this.lineThree = false
            this.vacationDetail.leaderTwoStatus = '同意'
            this.vacationDetail.leaderOneStatus = '同意'
            this.vacationDetail.leaderThreeStatus = this.statusThreeObj[result.data.zhgl_qj01018]
          }
        } else {
          MessageBox.alert(result.msg, '操作提示')
        }
      }, (rep) => {
        Indicator.close()
        Toast({
          message: '服务器异常',
          position: 'middle',
          duration: 1000
        })
      })
    },
    refuse: function () {
      let token = this.$store.getters.getUser.token
      let param = {
        zhgl_qj01027: '02', // 01 窗口人员    02 审批局（传固定值）
        zhgl_qj01010: '', // 值班长审核意见
        zhgl_qj01011: this.vacationDetail.zhgl_qj01011, // 审核人-值班长
        zhgl_qj01013: '', // 分管领导审核意见
        zhgl_qj01014: this.vacationDetail.zhgl_qj01014 || '', // 审核人-分管领导
        zhgl_qj01023: this.vacationDetail.zhgl_qj01023 || '', // 审核人-主要领导
        zhgl_qj01024: this.vacationDetail.zhgl_qj01024 || '', // 主要领导意见
        id: this.id, // 主 键id（打开审核页面时存储的id）
        token
      }
      Indicator.open({
        text: '正在加载...',
        spinnerType: 'fading-circle'
      })
      API.CHECK_REFUSE(this, param).then((rep) => {
        let result = rep.body
        Indicator.close()
        if (result.success) {
          let instance = Toast('驳回成功！2s后返回上一页')
          setTimeout(() => {
            instance.close()
            this.$router.go(-1)
          }, 2000)
        } else {
          MessageBox.alert(result.message, '操作提示')
        }
      }, (rep) => {
        Indicator.close()
        Toast({
          message: '服务器异常',
          position: 'middle',
          duration: 1000
        })
      })
    },
    agree: function () {
      let token = this.$store.getters.getUser.token

      let param = {
        zhgl_qj01027: '02', // 01 窗口人员    02 审批局（传固定值）
        zhgl_qj01010: '', // 值班长审核意见
        zhgl_qj01011: this.vacationDetail.zhgl_qj01011, // 审核人-值班长
        zhgl_qj01013: '', // 分管领导审核意见
        zhgl_qj01014: this.vacationDetail.zhgl_qj01014, // 审核人-分管领导
        zhgl_qj01023: this.vacationDetail.zhgl_qj01014, // 审核人-主要领导
        zhgl_qj01024: '', // 主要领导意见
        zhgl_qj01008: this.vacationDetail.zhgl_qj01008, // 请假天数
        id: this.id, // 主 键id（打开审核页面时存储的id）
        token
      }
      Indicator.open({
        text: '正在加载...',
        spinnerType: 'fading-circle'
      })
      API.CHECK_PASS(this, param).then((rep) => {
        let result = rep.body
        Indicator.close()
        if (result.success) {
          MessageBox.alert('审核成功！3后返回上一页', '操作提示')
          setTimeout(() => {
            this.$router.go(-1)
          }, 3000)
        } else {
          MessageBox.alert(result.msg, '操作提示')
        }
      }, (rep) => {
        Indicator.close()
        Toast({
          message: '服务器异常',
          position: 'middle',
          duration: 1000
        })
      })
    }
  }
}




